import React, { Component } from 'react';
import { Link } from "react-router-dom"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import './Header.css';


class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showDropdownMenu: false,
        }
    }

    render() {
        return <div class="main-header">
            <img class="title" src="title.png"/>
            <div class="hamburger-menu" onClick={()=>{this.setState({showDropdownMenu: !this.state.showDropdownMenu})}}>
                <FontAwesomeIcon icon={faBars}/>
            </div>
            <ul class={"main-menu " + (this.state.showDropdownMenu ? 'visible' : 'invisible')} 
                onClick={()=>{this.setState({showDropdownMenu: false})}}>
                <li>
                    <Link to="/">Rólunk</Link>
                </li>
                <li>
                    <Link to="/stores">Kiadó rakráraink</Link>
                </li>
                <li>
                    <Link to="/contact">Kapcsolatok</Link>
                </li>
            </ul>
            </div>
    }
}

export default Header;
