import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faWarehouse } from '@fortawesome/free-solid-svg-icons'
import './RentPage.css'

class Store extends Component {
    render() {
        return <div class="store">
                    <div class="store-icon">
                        <FontAwesomeIcon icon={faWarehouse}/>
                    </div>
                    <div class="store-data">
                        <div class = "store-header">
                            <h2>{this.props.store.name}</h2>
                        </div>
                        <div class = "store-info">
                            <span>Terület:</span>
                            <span>{this.props.store.area} m<sup>2</sup></span>
                        </div>
                        <div class = {"store-info"+ (!this.props.store.places ? ' hidden' : '')}>
                            <span>Rakathely:</span>
                            <span>{this.props.store.places}</span>
                        </div>
                        <div class = "store-info">
                            <span>{this.props.store.insulated ? 'Hőszigetelt' : 'Nem hőszigetelt'}</span>
                        </div>
                    </div>
               </div>

    }
}

